"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface Property {
  id: string;
  title: string;
  area: string;
  ownerName: string;
  winterPrice: number;
  summerPrice: number;
  winterAvailable: string[];
  status: string;
}

export default function WinterPeriodsPage() {
  const [properties, setProperties] = useState<Property[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchWinterProperties();
  }, []);

  const fetchWinterProperties = async () => {
    setIsLoading(true);
    setError(null);

    try {
      // Simulate API call to get properties with winter availability
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      const mockProperties: Property[] = [
        {
          id: 'property_mock_1',
          title: 'Apartamento Moderno en Centro',
          area: 'Centro',
          ownerName: 'Ana Martínez',
          winterPrice: 1200,
          summerPrice: 1800,
          winterAvailable: ['2024-09-01', '2025-06-30'],
          status: 'available'
        },
        {
          id: 'property_mock_2',
          title: 'Casa con Jardín en Salamanca',
          area: 'Salamanca',
          ownerName: 'Carlos López',
          winterPrice: 2200,
          summerPrice: 2800,
          winterAvailable: ['2024-10-01', '2025-05-31'],
          status: 'available'
        },
        {
          id: 'property_mock_3',
          title: 'Estudio en Malasaña',
          area: 'Malasaña',
          ownerName: 'Laura Fernández',
          winterPrice: 900,
          summerPrice: 1200,
          winterAvailable: ['2024-09-15', '2025-06-15'],
          status: 'available'
        }
      ];

      setProperties(mockProperties);
    } catch (err) {
      console.error('Error fetching winter properties:', err);
      setError('Error al cargar las propiedades de invierno');
    } finally {
      setIsLoading(false);
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('es-ES', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary/5 to-secondary/5 py-12">
      <div className="max-w-6xl mx-auto px-6">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-secondary mb-4">Periodos de Invierno</h1>
          <p className="text-muted-foreground text-lg">
            Propiedades disponibles durante la temporada de invierno (Septiembre - Junio)
          </p>
        </div>

        {/* Winter Season Info */}
        <Card className="mb-8 bg-gradient-to-r from-blue-50 to-indigo-50 border-blue-200">
          <CardHeader>
            <CardTitle className="text-2xl text-blue-800">Temporada de Invierno 2024-2025</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid md:grid-cols-3 gap-4">
              <div className="text-center">
                <div className="text-3xl font-bold text-blue-600">Sep - Jun</div>
                <p className="text-blue-700">Periodo Completo</p>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-blue-600">{properties.length}</div>
                <p className="text-blue-700">Propiedades Disponibles</p>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-blue-600">
                  €{properties.length > 0 ? Math.round(properties.reduce((sum, p) => sum + p.winterPrice, 0) / properties.length) : 0}
                </div>
                <p className="text-blue-700">Precio Promedio</p>
              </div>
            </div>
          </CardContent>
        </Card>

        {error && (
          <Alert className="border-red-200 bg-red-50 mb-6">
            <AlertDescription className="text-red-800">
              {error}
            </AlertDescription>
          </Alert>
        )}

        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin w-12 h-12 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
            <p className="text-muted-foreground">Cargando propiedades de invierno...</p>
          </div>
        ) : properties.length === 0 ? (
          <Card>
            <CardContent className="pt-6 text-center">
              <p className="text-muted-foreground text-lg mb-4">
                No hay propiedades disponibles para el periodo de invierno
              </p>
              <Button 
                onClick={() => window.location.href = '/property/new'}
                className="bg-primary hover:bg-primary/90 text-secondary"
              >
                Publicar Primera Propiedad
              </Button>
            </CardContent>
          </Card>
        ) : (
          <div className="grid gap-6">
            {properties.map((property) => (
              <Card key={property.id} className="overflow-hidden hover:shadow-lg transition-shadow duration-200">
                <CardHeader>
                  <div className="flex justify-between items-start">
                    <div>
                      <CardTitle className="text-xl mb-2">{property.title}</CardTitle>
                      <div className="flex items-center gap-2 mb-2">
                        <Badge variant="outline">{property.area}</Badge>
                        <Badge className="bg-blue-500 text-white">Invierno</Badge>
                        <Badge variant="secondary">{property.status === 'available' ? 'Disponible' : 'Ocupado'}</Badge>
                      </div>
                    </div>
                    <div className="text-right">
                      <div className="text-3xl font-bold text-primary">
                        €{property.winterPrice}
                      </div>
                      <p className="text-sm text-muted-foreground">por mes</p>
                      <p className="text-xs text-muted-foreground mt-1">
                        Verano: €{property.summerPrice}
                      </p>
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="grid md:grid-cols-2 gap-6">
                    <div>
                      <h4 className="font-semibold mb-3">Información de la Propiedad</h4>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span>Propietario:</span>
                          <span className="font-medium">{property.ownerName}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Zona:</span>
                          <span className="font-medium">{property.area}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Estado:</span>
                          <Badge variant={property.status === 'available' ? 'default' : 'secondary'}>
                            {property.status === 'available' ? 'Disponible' : 'Ocupado'}
                          </Badge>
                        </div>
                      </div>
                    </div>

                    <div>
                      <h4 className="font-semibold mb-3">Disponibilidad de Invierno</h4>
                      <div className="space-y-2 text-sm">
                        <div className="flex justify-between">
                          <span>Inicio:</span>
                          <span className="font-medium">{formatDate(property.winterAvailable[0])}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Fin:</span>
                          <span className="font-medium">{formatDate(property.winterAvailable[1])}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Duración:</span>
                          <span className="font-medium">10 meses</span>
                        </div>
                      </div>

                      <div className="mt-4 p-3 bg-blue-50 rounded-lg">
                        <p className="text-sm text-blue-800 font-medium">
                          Ahorro vs Verano: €{property.summerPrice - property.winterPrice}/mes
                        </p>
                      </div>
                    </div>
                  </div>

                  <div className="mt-6 flex gap-3">
                    <Button 
                      className="bg-primary hover:bg-primary/90 text-secondary"
                      onClick={() => window.location.href = `/property/${property.id}`}
                    >
                      Ver Detalles
                    </Button>
                    <Button 
                      variant="outline"
                      onClick={() => window.location.href = `/match`}
                    >
                      Buscar Inquilinos
                    </Button>
                    <Button 
                      variant="outline"
                      onClick={() => window.location.href = `/contract`}
                    >
                      Generar Contrato
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}

        <div className="text-center mt-8">
          <Button 
            variant="ghost" 
            onClick={() => window.location.href = '/'}
            className="text-muted-foreground hover:text-secondary"
          >
            Volver al Inicio
          </Button>
        </div>
      </div>
    </div>
  );
}
